:root {
  --main-font: "Roboto", sans-serif;
  --main-txt-cl: #000;
  --accent-txt-cl: #b92f2c;
  --btn-txt-cl: #000;
  --btn-txt-cl: #fff;
  --copyright-txt-cl: #545454;
  --modal-title: #8c8c8c;
  --bg-cl: #fff;
  --footer-bg-cl: #f7f7f7;
  --btn-bg-color: #000;
  --btn-radius: 100px;
  --duration: 250ms;
  --timing-function: linear;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--main-font);
  color: var(--main-txt-cl);
  background-color: var(--bg-cl);
  letter-spacing: .02em;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  cursor: pointer;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border-radius: var(--btn-radius);
  border: none;
  display: block;
}

#pagination {
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  display: flex;
}

#next5Button, #prev5Button, #firstPageButton {
  color: #000;
  background-color: #fff;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pageNumberButton, #totalPagesButton {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  outline: none;
  padding: 13px;
}

.pageNumberButton.active {
  color: #fff;
  background-color: #b92f2c;
  border-radius: 5px;
  position: absolute;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

#prevButton, #nextButton {
  color: #000;
  cursor: pointer;
  background-color: #f7f7f7;
  border: none;
  outline: none;
  padding: 13px;
}

